$twocolHeaderHeight: 40px;
.two-column {

#nav-bar {
 position: sticky;
 overflow:auto;
 top: 0;
 border-radius: 10px;
 max-width: fit-content;
 margin-left: auto;
 margin-right: auto;
 margin-bottom: 1%;
 margin-top:1%;
 background-color: lightgray;
 white-space: nowrap;
 }

 /* Navbar links */
 #nav-bar a {
 float: left;
 display: block;
 color: black;
 text-align: center;
 padding: 14px;
 text-decoration: underline;
 border-bottom: none;
 }

.sticky-frame {
 position: sticky;
 position: -webkit-sticky;
 top: 5%;
 max-height: 25vh;
 box-sizing: border-box;
 width: 20%;
}

.wrapper {
 display: flex;
 justify-content: space-between;
}
.sidebar {

 h3 {
 margin-top: 0px;
 }
 ol {
 padding: 0px;
 margin: 0px;
 }
 .side-menu {
 background: #F6F6F6;
 padding: 0.5em;
 a {
 font-size: 1.05em;
 text-overflow: ellipsis;
 overflow: hidden;
 padding-right: 20px !important;
 }
 a:hover {
 background: #EEE;
 }
 
 >li {
 a {
 display: flex;
 &.active {
 background: $lightpurple;
 color: white;
 }
 }
 
 >a:first-child {
 padding: 4px 0px 3px 10px;
 font-weight: 500;
 }
 ol {
 margin-top: 0px;
 margin-bottom: 10px;
 li {
 a {
 color: #444;
 font-size: 0.9em;
 padding-left: 20px;
 }

 ol li {
 a {
 color: #666;
 font-size: 0.9em;
 padding-left: 45px;
 }
 ol li > a {
 padding-left: 65px;
 }
 }
 }
 }
 }
 li {
 margin-top: 0px;
 
 line-height: 1.2em;
 }
 }
}
 /* Large rounded green border */
 hr {
 border: 2px solid grey;
 border-radius: 5px;
 width:100%;
 }

 main {
 h1, h2, h3, h4 {
 padding-top: 85px;
 margin-top: -70px;
 }
 h2, h3, h4 {
 z-index: -1;
 }
 }
 .desktop { display: flex; }
 .mobile { display: none; }
 header {
 height: 116px;
 width: 100vw;
 display: flex;
 align-items: center;
 justify-content: center;
 z-index: 100;
 h1 {
 z-index: 100;
 }
 }
 nav {
 height: 48px;
 // background: white;
 border-bottom: dotted 1px #CCC;
 width: 100vw;
 display: flex;
 align-items: center;
 justify-content: center;
 ul {
 z-index: 300;
 }
 }

 aside {
 position: sticky;
 //top: $twocolHeaderHeight;
 top: 0;
 left: 0px;
 min-height: 100vh;
 width: 20%;
 overflow-y: auto;
 padding-top: 25px;
 padding-bottom: 40px;

 }
 article {
 display:flex;
 width: 100%;
 margin-top: 10px;

 padding-left: 0em;
 h1 {
 font-size: 1.8em;
 }
 }
}
