.container {
 grid-template-columns: 1fr;
 grid-template-areas:
 "top"
 "menu"
 "main"
 "bottom";
}

.assignments, .schedule {
 .passed {
 background: #F9F9F9;
 strong {
 color: #777;
 }
 }
}

.assignments, .lectures, .readings, .syllabus, .schedule {
 .passed {
 // color: #777;
 // td, th {
 // background: #F9F9F9;
 // }
 // li, li::before {
 // color: #777;
 // }
 .hw, a.hw,
 .pj, a.pj,
 .lec, a.lec,
 .lab, a.lab,
 .quiz, a.quiz,
 .quiz, a.quizzes,
 .ec, a.ec {
 // opacity: 0.6;
 background: #707070;
 }
 strong, th {
 font-weight: 200;
 color: #5c5962;
 }
 }
}


.assignments, .lectures, .assignment, .reference, .lecture, .readings, .link-menu, .two-column article, .syllabus, .schedule {
 display: flex;
 flex-direction: column;

 .badge, a.badge,
 .hw, a.hw,
 .pj, a.pj,
 .lec, a.lec,
 .lab, a.lab,
 .quiz, a.quiz, a.quizzes,
 .live, .recorded,
 .ec, a.ec {
 display: inline-block;
 border-radius: 4px;
 color: white;
 text-transform: uppercase;
 font-size: .90em;
 line-height: 1.2em;
 text-decoration: none;
 padding: 3px 7px;
 border-bottom: solid transparent 1px;
 min-width: 42px;
 margin-right: 5px;
 text-indent: 0px;
 
 }
 .badge, .badge:hover {
 background: None;
 color: #444;
 }
 .quizzes{
 background: $orange;
 }
 .hw, .hw:hover {
 background: $purple;
 }
 .quiz, .quiz:hover {
 background: $mutedblue;
 }
 .pj, .pj:hover {
 background: $blue;
 }
 .lec, .lec:hover, a.lec {
 background: $lightlightpurple;
 }
 .lab, .lab:hover {
 background: $green;
 font-weight: 600;
 }
 .ec, .ec:hover {
 background: rgba(104, 74, 152, 0.5);
 // color: #444 !important;
 font-weight: 600;
 }
 .live {
 background: $orange;
 }
 .recorded {
 background: $lightpurple;
 }

 ul {
 margin-top: 0px;
 }
}

.readings {
 h3 {
 font-size: 1em;
 font-weight: 500;
 // font-family: 'Akkurat Pro Regular', sans-serif;
 }
 li {
 margin-bottom: 8px;
 }
}

.two-column {
 .outline {
 background: #F6F6F6;
 padding: 10px;
 border-radius: 10px;
 margin-bottom: 15px;
 padding: 0px;
 margin: 0px;
 ol, ul {
 padding: 10px 10px 5px 15px;
 }
 li {
 
 padding: 0px;
 margin-bottom: 5px;
 a {
 border-bottom: dotted 1px #999;
 }
 }
 }
}

.assignment, .reference, .lecture, .two-column article, .syllabus, .schedule {
 ul.toc {
 padding-left: 0px;
 li {
 margin-left: 10px;
 }
 li::before {
 content: none;
 }
 }
 .outline {
 background: #F6F6F6;
 padding: 10px;
 border-radius: 10px;
 margin-bottom: 15px;
 h3 {
 margin-top: 0px;
 }
 }
 .checkbox-list {
 padding-left: 22px;
 }

 .checkbox-list > li {
 text-indent: -8px;
 margin-left: 22px;
 }

 .checkbox-list > li:before {
 font-family:"Font Awesome 5 Pro";
 content: "\f00c";
 font-weight: 600;
 color: #1cb954;
 font-size: 1.2em;
 vertical-align: top;
 margin-right: 6px;
 }
}

ol.readings, ul.readings {
 margin-top: 0px;
 margin-bottom: 0px;

 li {
 margin-top: 0px;
 }
}

.lectures, .schedule, .readings {
 ul {
 margin: 0px;
 }
 p, li {
 margin-top: 0px;
 }
 ol {
 margin-top: 0px;
 margin-bottom: 0px;
 padding-left: 14px;
 li {
 margin-bottom: 5px;
 }
 }
}

blockquote.caveat {
 margin: 20px 0px 20px 40px;
 background: none;
 border-left: 0px;
 padding: 25px;
 padding: 0px;
}

blockquote.quote {
 margin-left: 20px;
 background: none;
 border-left: 0px;
 padding: 25px;
 font-size: 1.2em;
 font-weight: 500;
 margin-bottom: 30px;

 p:before {
 content: '“';
 color: $lightgrey;
 font-size: 2em;
 line-height: 0.1em;
 margin-right: 0.1em;
 vertical-align: -0.4em;
 font-family: 'Times New Roman', Times, serif;
 vertical-align: middle;
 }
 &.slim {
 border-left: solid 10px #EEE;
 // margin: 0px;
 color: #BBB;
 // font-size: 1.4em;
 // font-weight: 600;
 margin-left: 30px;
 background: #FCFCFC;
 padding-left: 20px;
 padding-bottom: 0px;
 p {
 margin-bottom: 20px;
 font-weight: 400;
 color: #aaa;
 font-size: 20px;
 }
 }
 &.slim:before {
 font-size: 3em;
 }
}

.schedule, .labs {
 ul { padding-left: 20px; }
}

.assignment, .lecture, .details, .two-column article, .syllabus, .schedule {
 .outline {
 margin-top: 30px;
 }
 .summary {
 background: #F3F3F3;
 padding: 10px;
 li {
 margin-left: 30px;
 }
 }
 ul {
 
 li::before {
 font-size: 20px;
 content: "\2022";
 font-weight: bold;
 display: inline-block;
 text-indent: -16px;
 vertical-align: middle;
 color: $purple;
 }
 }
 h2 {
 border-bottom: solid 1px $lightgrey;
 }
 h3 {
 color: #676767;
 font-family:"Campton Extra Light";
 // color: $lightpurple;
 font-size: 1.5em;
 }
}
